.bottom-btns {
  width: 100%;
  height: 80px;
  display: flex;
  flex: 0 0 auto;
  padding-top: 30px;
  align-items: center;
  justify-content: center;
  @media(max-width: 1180px) {
    position: relative;
    justify-content: flex-end;
    border-top: none;
    background-color: #fff
  }
}

.bottom-btns__submit-btn {
  margin-left: 20px;
  @media(max-width: 1180px) {
    margin-right: 5%
  }
}

.bottom-btns__delete-btn {
  margin: 0px 15px;
  @media(max-width: 1180px) {
    display: none
  }

  @media(max-width: 540px) {
    display: none
  }
}

.bottom-btns__save-btn {
  @media(min-width: 1180px) {
    background-color: #e9ecef;
    color: #000000;
    &:hover, &:focus {
      background-color: #dde0e4
    };

    &:active {
      background-color: #c8cdd3
    }
  };

  @media(max-width: 1180px) {
    margin-right: 5%;
    margin-left: 10px
  }
}