.delete-btn {
  width: 100%;
  background-color: #fff;
  color: #ff3333;
  text-align: center;
  height: 60px;
  font-family: TextBook, sans-serif;
  font-weight: normal;
  font-size: 17px;
  cursor: pointer;
  border: none;
  transition: .15s ease-in-out;
  outline: none;
  &:focus, &:active {
    color: #f00;
  }
}