.input {
  width: calc(100% - 10px);
  height: 35px;
  padding-left: 10px;
  border: 2px solid #f0f2f4;
  color: #000;
  font-family: TextBook, sans-serif;
  font-size: 16px;
  font-weight: normal;
  border-radius: 3px;
  outline: none;
  transition: all .15s ease-in-out;
  &::-webkit-input-placeholder, &::-moz-placeholder, &:-moz-placeholder {
    color: #c0c5ca;
  };
  &:hover, &:focus {
    border: 2px solid #55a8fe;
  }
}

.input--error {
  border-color: '#f95345';
}