.room-card {
  width: 100%;
  height: 40px;
  background-color: #e9ecef;
  border-radius: 3px;
  position: relative;
  margin-top: 10px;
  cursor: pointer;
  border: none;
  outline: none;
  transition: .15s ease-in-out;
  &:hover, &:focus {
    background-color: #dde0e4;
  };

  &:disabled {
    background-color: #e1e5e8;
    color: #c8cbcf;
  };

  &:active {
    background-color: #c8cdd3;
  }
}

.room-card--selected {
  background-color: #007dff;
  &:hover, &:focus {
    background-color: #0059ff;
  };

  &:active {
    background-color: #0011fd;
  }

  .room-card__paragraph, .room-card__headline {
    color: #fff;
  }
}

.room-card__inner {
  height: 100%;
  display: flex;
  align-items: center;
  margin-left: -5px;
}

.room-card__headline {
  padding-left: 10px;
}

.room-card__paragraph {
  margin-left: 20px;
  text-align: left;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.room-card__close-icon {
  position: absolute;
  right: 10px;
  background-color: transparent;
  color: #fff;
  font-size: 16px;
  margin-top: -8px;
}