.grey-btn {
  padding: 0px 15px;
  height: 40px;
  border-radius: 3px;
  background-color: #e9ecef;
  color: #000000;
  font-family: TextBook, sans-serif;
  font-weight: normal;
  border: none;
  font-size: 16px;
  cursor: pointer;
  transition: .15s ease-in-out;
  outline: none;
  &:hover, &:focus {
    background-color: #dde0e4;
  };

  &:disabled {
    background-color: #e1e5e8;
    color: #c8cbcf;
  };

  &:active {
    background-color: #c8cdd3;
  };

  @media(max-width: 540px) {
    padding: 0px 20px
  }
}