.dropdown-option {
  height: 35px;
  cursor: pointer;
  display: flex;
  background-color: #ffffff;
  align-items: center;
  padding: 0px 15px;
  transition: all .15s ease-in-out;
  &:hover {
    background-color: #f6f7f9;
  }
}

.dropdown-option__image-container {
  width: 25px;
  height: 25px;
  margin-right: 10px;
}

.dropdown-option__avatar {
  width: 100%;
  height: 100%;
  border-radius: 100%;
  object-fit: cover;
}

.dropdown-option__paragraph {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.dropdown-option__grey-paragraph {
  font-weight: normal;
  font-size: 16px;
  text-transform: lowercase;
  white-space: nowrap;
}

.dropdown-option__floor-number {
  font-size: 17px;
}

.dropdown-option__highlighted {
  font-weight: bold;
}

.dropdown-option__active {
  background-color: #f6f7f9;
}