.time-input {
  display: flex;
  align-items: center;
  width: 100%;
  height: 37px;
  padding-left: 10px;
  border-radius: 3px;
  border: 2px solid #f0f2f4;
  box-sizing: content-box;
  color: #000;
  transition: all .15s ease-in-out;
  &:hover {
    border: 2px solid #55a8fe
  }

  &::-webkit-input-placeholder, &::-moz-placeholder, &:-moz-placeholder {
    color: #c0c5ca
  }
}

.time-input--focused {
  border: 2px solid #55a8fe;
}

.time-input--error {
  border-color: #f95345;
}

.time-input__colon {
  position: relative;
  left: 1px;
}

.time-input__invisible-input {
  border: none;
  background-color: transparent;
  font-family: TextBook, sans-serif;
  font-size: 16px;
  font-weight: normal;
  border-radius: 3px;
  transition: all .15s ease-in-out;
  text-align: right;
  outline: none;
  width: 18px;
  height: 100%;
  overflow: hidden;
}