.round-btn {
  width: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 25px;
  background-color: #e8ecef;
  border-radius: 50%;
  border: none;
  color: #8c8d8f;
  cursor: pointer;
  outline: none;
  transition: .15s ease-in-out;
  &:hover, &:focus {
    color: #2a2b2c
  };

  &:disabled {
    background-color: #e1e5e8;
    color: #c8cbcf
  };
}