.meeting {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: #fff;
  z-index: 100;
  @media(max-width: 1180px) {
    display: block
  }
}

.meeting__inner {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  flex: 1 0 auto
}

.meeting__headline {
  margin-bottom: 20px;
  outline: none;
  @media(max-width: 1180px) {
    &:first-child {
      align-self: flex-start;
      margin-left: 5%
    }
  }
}

.meeting__close-btn {
  position: absolute;
  right: 0px;
  top: -55px;
  @media(max-width: 1180px) {
    display: none
  }
}

.meeting__delete-btn {
  border-bottom: 10px solid #e9ecef;
  @media(min-width: 1180px) {
    display: none;
  }
}

.meeting-section {
  width: calc(66% + 40px);
  max-width: 1100px;
  position: relative;
  display: flex;
  justify-content: space-between;
  @media(max-width: 1180px) {
    width: 100%;
    background-color: #fff;
    flex-direction: column;
    align-items: center;
    justify-content: unset;
    border-bottom: 10px solid #e9ecef;
    padding-bottom: 20px;
    &:first-child {
      border-bottom: none;
    }
  }
}

.meeting-section__subsection {
  width: calc(50% - 20px);
  @media(max-width: 1180px) {
    width: 90%;
    margin: 0px
  }
}