.participator-card {
  width: auto;
  background-color: #e9ecef;
  height: 25px;
  display: inline-flex;
  border-radius: 25px;
  position: relative;
  align-items: flex-start;
  margin-top: 10px;
  margin-left: 10px;
  padding-right: 35px;
  border: none;
  cursor: pointer;
  outline: none;
  transition: .15s ease-in-out;
  &:focus, &:hover {
    background-color: #dfe4e8
  };

  @media(max-width: 540px) {
    width: 100%;
    height: 30px;
    margin-left: 5px;
    padding-right: 0px
  }
}

.particitapor-card__image-container {
  width: 25px;
  height: 25px;
  margin-left: -10px;
  margin-top: -1px;
  @media(max-width: 540px) {
    width: 30px;
    height: 30px
  }
}

.participator-card__avatar {
  width: 100%;
  height: 100%;
  border-radius: 100%;
  object-fit: cover;
}

.participator-card__paragraph {
  margin-top: 0;
}

.participator-card__close-icon {
  color: #b3b8bc;
  position: absolute;
  right: 10px;
  margin-top: 3px;
  @media(max-width: 540px) {
    margin-top: 6px
  }
}

.participator-card__paragraph {
  margin-left: 10px;
  margin-top: 3px;
  @media(max-width: 540px) {
    font-size: 17px
  }
}